.contacts {
  display: grid;
  grid-template-columns: minmax(20%, 560px) 1fr;
  padding-block: 120px;
  color: var(--themed-dark);

  @include vp-1023 {
    grid-template-columns: minmax(324px, 1fr) 53%;
    padding-block: 100px;
  }

  @include vp-767 {
    grid-template-columns: 1fr;
    padding-block: 58px 65px;
  }
}

.contacts__map {
  @include vp-767 {
    order: 2;
  }
}

.contacts__text {
  padding-top: 75px;
  padding-left: 16.5%;

  @include vp-1023 {
    padding-top: 66px;
    padding-left: 8.5%;
  }

  @include vp-767 {
    padding-top: 0;
    padding-left: 0;
    margin-bottom: 36px;
  }

  p {
    margin: 0;
    margin-bottom: 32px;
    font: 500 18px / 1.5 $ff-montserrat;

    @include vp-1023 {
      margin-bottom: 28px;
    }
  }

  p:last-child {
    margin-bottom: 0;
  }
}

.contacts__address {
  p {
    display: flex;
    margin: 0;
    margin-bottom: 21px;
    font: 400 16px / 1.5 $ff-montserrat;

    @include vp-1023 {
      max-width: 27ch;
      margin-bottom: 20px;
    }

    &::before {
      --icon-url: var(--address-icon-url);

      content: "";
      display: block;
      flex-shrink: 0;
      width: 22px;
      min-width: 22px;
      height: 22px;
      margin-right: 6px;
      background-color: var(--themed-dark);
      mask: var(--icon-url) no-repeat center top / contain;

      @include vp-1023 {
        margin-top: 3px;
      }
    }
  }

  p:last-child {
    margin-bottom: 0;
  }

  a {
    display: flex;
    font: 400 16px / 1.5 $ff-montserrat;

    &::before {
      --icon-url: var(--phone-icon-url);

      content: "";
      display: block;
      flex-shrink: 0;
      width: 20px;
      min-width: 20px;
      height: 20px;
      margin-right: 8px;
      background-color: var(--themed-dark);
      mask: var(--icon-url) no-repeat center top / contain;
    }
  }

  a:not(:last-child) {
    margin-bottom: 5px;
  }
}
